section {
    color: #8a8fa3;
    margin-top: 75px;
}
footer {
    background-color: #777;
    bottom: 0;
    color: #ccc;
    margin-top: 30px;
    position: relative;
    z-index: 1;
}
footer p {
    padding: 15px;
    text-align: center;
}
.about p {
    line-height: 180%;
}
.about img {
    border: 1px solid rgba(0,0,0,.125);
    margin-bottom: 40px;
}
.center {
    text-align: center;
}
.about .col.center img, .about .card {
    box-shadow: 0px 0px 21px 0px rgb(152 152 152 / 23%);
}
.card.small {
    margin-top: 40px;
    text-align: center;
    white-space: nowrap;
}

